import { request } from "./request";
import Vue from 'vue'

export function templateQuery(id) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/templateQuery?id=" +id,
        method: 'POST'
    })
}
export function templateAdd(rationId,topName,topType) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/templateAdd?rationId=" +rationId+'&topName='+topName+'&topType='+topType,
        method: 'POST'
    })
}
export function templateUpdate(rationId,topName,topType,recId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/templateUpdate?rationId=" +rationId+'&topName='+topName+'&topType='+topType+'&recId='+recId,
        method: 'POST'
    })
}
export function templateDelete(recId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/templateDelete?recId="+recId,
        method: 'POST'
    })
}
export function paramsQuery(id) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/paramsQuery?id="+id,
        method: 'POST'
    })
}
export function paramsAdd(data) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/paramsAdd",
        data:data,
        method: 'POST'
    })
}
export function paramsUpdate(data) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/paramsUpdate",
        data:data,
        method: 'POST'
    })
}
export function paramsDelete(recId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/paramsDelete?recId="+recId,
        method: 'POST'
    })
}