<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <el-button
        type="primary"
        icon="el-icon-plus"
        style="
          background-color: #0066ff;
          border: none;
          margin-bottom: 0.15rem;
          width: 1.5rem;
        "
        @click="addInfo"
        >新增</el-button
      >
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="100%">
          <el-table-column prop="rationId" label="关联编号"> </el-table-column>
          <el-table-column prop="topName" label="拓扑图名称"> </el-table-column>
          <el-table-column label="拓扑图类型">
            <template slot-scope="scope">
              {{ getTopTypeName(scope.row.topType) }}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="editInfo(scope.row)"
                >修改</el-button
              >
              <el-button type="text" size="small" @click="deleteInfo(scope.row)"
                >删除</el-button
              >
              <el-dropdown style="margin-left: 10px">
                <el-button type="text" size="small">更多</el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    @click.native="toLookParameterConfig(scope.row)"
                    >查看</el-dropdown-item
                  >
                  <el-dropdown-item @click.native="toParameterConfig(scope.row)"
                    >参数配置</el-dropdown-item
                  >
                  <el-dropdown-item @click.native="draw(scope.row)"
                    >绘制图表</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <eleDraw ref="eleDrawDialog"></eleDraw>
    <digitalTwinAdd ref="add"></digitalTwinAdd>
    <!-- 参数配置弹框 -->
    <parameterConfigDialog ref="parameterConfig"></parameterConfigDialog>
    <lookEleDraw ref="lookEleDrawDialog"></lookEleDraw>
  </div>
</template>
<script>
import eleDraw from "./components/eleDraw.vue";
import lookEleDraw from "./components/lookEleDraw.vue";
import digitalTwinAdd from "./components/digitalTwinAdd.vue";
import parameterConfigDialog from "./components/parameterConfig.vue";
import { elecPlaces } from "@/network/energyTrendAnalysis";
import { templateQuery, templateDelete } from "@/network/digitalTwin";
export default {
  components: {
    eleDraw,
    digitalTwinAdd,
    parameterConfigDialog,
    lookEleDraw,
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      tableData: [],
    };
  },
  created() {
    this.$bus.$on("digitalRefresh", (msg) => {
      this.templateQueryFun();
    });
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    /**
     * 获取当前用户下的区域公司层级信息
     * **/
    placesFun() {
      elecPlaces()
        .then((data) => {
          let res = data.content
          let flag = false;
          res.forEach((item) => {
            if (!flag) {
              this.areaId = item.id;
              flag = true;
            }
          });
          this.data = this.$arrToTree(res);
          this.templateQueryFun();
        })
        .catch((err) => {});
    },
    nodeClick(item) {
      this.areaId = item.id;
      this.templateQueryFun();
    },
    /**
     * 查看绘制图表
     * **/
    toLookParameterConfig(row) {
      this.$refs.lookEleDrawDialog.showDialogFun(row.topId);
    },
    /**
     * 绘制图表
     * **/
    draw(row) {
      this.$refs.eleDrawDialog.showDialogFun(row.topId);
    },
    /**
     * 查询绘制图表列表
     * **/
    templateQueryFun() {
      templateQuery(this.areaId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 新增模板
     * **/
    addInfo() {
      this.$refs.add.showDialogFunAdd(this.areaId);
    },
    /**
     * 修改模板
     * **/
    editInfo(row) {
      this.$refs.add.showDialogFunEdit(row);
    },
    /**
     * 删除模板
     * **/
    deleteInfo(row) {
      this.$alert("确认删除该条信息", "", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.templateDeleteFun(row.recId);
        },
      });
    },
    templateDeleteFun(recId) {
      templateDelete(recId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "删除成功",
              type: "success",
            });
            this.templateQueryFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 参数配置
     * **/
    toParameterConfig(row) {
      this.$refs.parameterConfig.showDialogFun(row);
    },
    /**
     * 根据typeId获取Name
     * **/
    getTopTypeName(typeId) {
      let name = "";
      if (typeId == 0) {
        name = "首页园区图";
      } else if (typeId == 1) {
        name = "配电图";
      } else if (typeId == 2) {
        name = "网络拓扑图";
      } else if (typeId == 3) {
        name = "电站布线top图";
      }
      return name;
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
}
.right {
  flex-grow: 1;
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  // align-items: flex-end;
  padding: 0.2rem 0.2rem;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  //   margin: 0.2rem 0 0;
  flex-grow: 1;
}
.chart {
  flex-grow: 1;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0.15rem 0.47rem;
  border: none;
  cursor: pointer;
  margin-bottom: 0.15rem;
  white-space: nowrap;
}
