}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
