<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      width="90%"
      :modal="false"
    >
      <div style="">
       <p style="text-align: right;">
         <el-button
          type="primary"
          icon="el-icon-plus"
          style="
            background-color: #0066ff;
            border: none;
            margin-bottom: 0.15rem;
            width: 1.5rem;
          "
          @click="addInfo"
          >新增</el-button
        >
       </p>
        <el-table v-if="tableData" :data="tableData" style="width: 100%" height="400">
          <el-table-column prop="topLineId" label="参数$key"></el-table-column>
           <el-table-column prop="topLineType" label="类型">
            <template slot-scope="scope">
              {{ getTopTypeName(scope.row.topLineType) }}
            </template>
          </el-table-column>
          <el-table-column prop="pointId" label="点位"> </el-table-column>
          <el-table-column prop="sQLContent" label="sql语句" width="300"> </el-table-column>
          <el-table-column prop="measureNo" label="电力系统设备编号"> </el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="editInfo(scope.row)"
                >修改</el-button
              >
              <el-button type="text" size="small" @click="deleteInfo(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
    <parameterConfigAdd ref="add"></parameterConfigAdd>
  </div>
</template>
<script>
import { paramsQuery, paramsDelete } from "@/network/digitalTwin";
import parameterConfigAdd from "./parameterConfigAdd.vue";
export default {
  components: {
    parameterConfigAdd,
  },
  data() {
    return {
      dialogVisible: false,
      tableData: [],
    };
  },
  watch: {},
  created() {
    this.$bus.$on("parameterRefresh", (msg) => {
      this.paramsQueryFun(this.rowInfo.recId);
    });
  },
  mounted() {},
  methods: {
    showDialogFun(row) {
      this.rowInfo = row;
      this.dialogVisible = true;
      this.paramsQueryFun(row.recId);
    },
    paramsQueryFun(recId) {
      paramsQuery(recId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 新增
     * **/
    addInfo() {
      this.$refs.add.showDialogFunAdd(this.rowInfo.topId);
    },
    /**
     * 修改模板
     * **/
    editInfo(row) {
      this.$refs.add.showDialogFunEdit(this.rowInfo.topId, row);
    },
    /**
     * 删除模板
     * **/
    deleteInfo(row) {
      this.$alert("确认删除该条信息", "", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.paramsDeleteFun(row.recId);
        },
      });
    },
    paramsDeleteFun(recId) {
      paramsDelete(recId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "删除成功",
              type: "success",
            });
            this.paramsQueryFun(this.rowInfo.recId);
          }
        })
        .catch((err) => {});
    },
    /**
     * 根据typeId获取Name
     * **/
    getTopTypeName(typeId) {
      let name = "";
      if (typeId == 0) {
        name = "配电房";
      } else if (typeId == 1) {
        name = "能源";
      } else if (typeId == 2) {
        name = "光伏";
      } else if (typeId == 3) {
        name = "sql语句";
      } else if (typeId == 4) {
        name = "电力系统";
      }
      return name;
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
</style>