<template>
  <div>
    <el-dialog title="" :visible.sync="dialogFormVisible" :modal="false">
      <el-form :model="form">
        <el-form-item label="拓扑图名称" :label-width="formLabelWidth">
          <el-input v-model="form.topName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="类型" :label-width="formLabelWidth">
          <el-select v-model="form.topType" placeholder="">
            <el-option
              v-for="(item, index) in topTypeOption"
              :key="index"
              :label="item"
              :value="index"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { templateAdd, templateUpdate } from "@/network/digitalTwin";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        topName: "",
        topType: "",
      },
      formLabelWidth: "120px",
      topTypeOption: ["首页园区图", "配电图", "网络拓扑图", "电站布线top图"],
    };
  },
  watch: {
    dialogFormVisible(val) {
      if (!val) {
        this.form = {
          topName: "",
          topType: "",
        };
      }
    },
  },
  mounted() {},
  methods: {
    showDialogFunAdd(rationId) {
      this.dialogFormVisible = true;
      this.rationId = rationId;
      this.dialogState = "add";
    },
    showDialogFunEdit(row) {
      this.form = {
        topName: row.topName,
        topType: row.topType,
      };
      this.dialogFormVisible = true;
      this.editRow = row;
      this.dialogState = "edit";
    },
    confirm() {
      if (this.dialogState == "add") {
        this.templateAddFun();
      } else {
        this.templateUpdateFun();
      }
    },
    templateAddFun() {
      templateAdd(this.rationId, this.form.topName, this.form.topType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "新增成功",
              type: "success",
            });
            this.dialogFormVisible = false;
            this.form = {
              topName: "",
              topType: "",
            };
            this.$bus.$emit("digitalRefresh", true);
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
    templateUpdateFun() {
      templateUpdate(
        this.editRow.rationId,
        this.form.topName,
        this.form.topType,
        this.editRow.recId
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "修改成功",
              type: "success",
            });
            this.dialogFormVisible = false;
            this.form = {
              topName: "",
              topType: "",
            };
            this.$bus.$emit("digitalRefresh", true);
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
    /**
     * 产能录入新增
     * **/
    // capacityInputAddFun() {
    //   let eqmName = "";
    //   this.eqmOption.forEach((item) => {
