    //       const { content, errcode, errmsg } = res;
    //       if (errcode == 0) {
    //         this.$message({
    //           message: "新增成功",
    //           type: "success",
    //         });
    //         this.dialogFormVisible = false;
    //         this.form = {
    //           areaVal: "",
    //           companyVal: "",
    //           date: "",
    //           outPutQty: "",
    //           productName: "",
    //           selectDepartment: "",
    //           selectEqmName: "",
    //           unitId: "",
    //         };
    //         this.$bus.$emit("unitRefresh", true);
    //       } else {
    //         this.$message.error(errmsg);
    //       }
    //     })
    //     .catch((err) => {});
    // },
    // /**
