<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal="false"
      width="95%"
      top="3vh"
    >
      <div
        style="width: 100%; height: 80vh; overflow: scroll; background: #fff"
        class="dialog_content"
      >
        <iframe
          :src="topId"
          frameborder="0"
          style="width: 100%; height: 100%"
        ></iframe>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible: false,
      topId: "",
    };
  },
  watch: {},
  mounted() {},
  methods: {
    showDialogFun(topId) {
      this.dialogVisible = true;
      this.topId =
        "http://" +
        this.$ipAddress +
        "/html/apps/jinghe/index.html?topId=" +
        topId;
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
.dialog_content {
  display: flex;
  flex-wrap: wrap;
  > div {
    margin: 0.25rem 0;
    width: 25%;
    color: #fff;
    display: flex;
    align-items: center;
    > p:first-child {
      color: #b8c6ff;
    }
  }
}
</style>