<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :modal="false"
      top="17vh"
    >
      <p style="padding-left:130px;color:#fff;text-align:left;margin-bottom:10px;font-size:12px">
        SQL语句规范（表名：JH_PDR_ANAPOINT_DATA_电站IDSTATIONNO_$yearMonth;替换为当前年月，$today;替换为当日，最终select“DATA_VALUE”）
      </p>
      <el-form :model="form">
        <el-form-item label="参数$key" :label-width="formLabelWidth">
          <el-input v-model="form.topLineId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="类型" :label-width="formLabelWidth">
          <el-select v-model="form.topLineType" placeholder="">
            <el-option
              v-for="(item, index) in topTypeOption"
              :key="index"
              :label="item"
              :value="index"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="点位" :label-width="formLabelWidth">
          <el-input v-model="form.pointId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="sql语句" :label-width="formLabelWidth">
          <el-input v-model="form.sqlContent" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电力系统设备编号" :label-width="formLabelWidth">
          <el-input v-model="form.measureNo" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { paramsAdd, paramsUpdate } from "@/network/digitalTwin";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        measureNo: "",
        pointId: "",
        sqlContent: "",
        topId: "",
        topLineId: "",
        topLineType: "",
      },
      formLabelWidth: "130px",
      topTypeOption: ["配电房", "能源", "光伏", "sql语句", "电力系统"],
    };
  },
  watch: {
    dialogFormVisible(val) {
      if (val == false) {
        this.form = {
          measureNo: "",
          pointId: "",
          sqlContent: "",
          topId: "",
          topLineId: "",
          topLineType: "",
        };
      }
    },
  },
  mounted() {},
  methods: {
    showDialogFunAdd(id) {
      this.dialogFormVisible = true;
      this.form.topId = id;
      this.dialogState = "add";
    },
    showDialogFunEdit(topId, row) {
      this.form = {
        measureNo: row.measureNo,
        pointId: row.pointId,
        sqlContent: row.sQLContent,
        topId: topId,
        topLineId: row.topLineId,
        topLineType: row.topLineType,
      };
      this.dialogFormVisible = true;
      this.editRow = row;
      this.dialogState = "edit";
    },
    confirm() {
      if (this.dialogState == "add") {
        this.paramsAddFun();
      } else {
        this.paramsUpdateFun();
      }
    },
    /**
     * 序列化
     * **/
    transformation(data) {
      let params = new URLSearchParams();
      //遍历json对象的每个key/value对,p为key
      for (var item in data) {
        params.append(item, data[item]);
      }
      return params;
    },
    paramsAddFun() {
      let data = {
        measureNo: this.form.measureNo,
        pointId: this.form.pointId,
        sqlContent: this.form.sqlContent,
        topId: this.form.topId,
        topLineId: this.form.topLineId,
        topLineType: this.form.topLineType,
      };
      let data1 = this.transformation(data);
      paramsAdd(data1)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "新增成功",
              type: "success",
            });
            this.dialogFormVisible = false;
            this.$bus.$emit("parameterRefresh", true);
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
    paramsUpdateFun() {
      let data = {
        measureNo: this.form.measureNo,
        pointId: this.form.pointId,
        sqlContent: this.form.sqlContent,
        topId: this.form.topId,
        topLineId: this.form.topLineId,
        topLineType: this.form.topLineType,
        recId: this.editRow.recId,
      };
      let data1 = this.transformation(data);
      paramsUpdate(data1)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "修改成功",
              type: "success",
            });
            this.dialogFormVisible = false;
            this.$bus.$emit("parameterRefresh", true);
          } else {
